	async loadRace(props = this.props) {
		this.setState({ isLoading: true });
		const { raceStore, userStore } = props.stores;
		const { user = {} } = userStore;
		const { raceid } = props;
		await raceStore.load(raceid);
		this.setState({ isLoading: false });

		let signup = {};
		const { race } = raceStore;
		if (race.classes && user && user.email) {
			for (let i = 0, l = race.classes.length; i < l; i += 1) {
				const currentClass = race.classes[i];
				if (currentClass.contestants) {
					const idx = currentClass.contestants.findIndex(e => e.email === user.email);
					if (idx > -1) {
						signup = { ...currentClass.contestants[idx] };
					}
				}
			}
		}
		raceStore.updateKeyValue('signup', signup);
	}

